import React from 'react';
import client1 from './images/brand1.jpg';
import client2 from './images/brand2.png';
import client4 from './images/brand3.png';
import client5 from './images/brand4.jpg';
import client6 from './images/brand5.png';
import client7 from './images/brand6.jpg';
import client8 from './images/brand7.png';
import client9 from './images/brand9.png';
import client10 from './images/brand10.png';
import client11 from './images/brand11.png';
import client12 from './images/brand12.jpg';
import client13 from './images/brand15.png';
import client14 from './images/brand16.png';



import aboutimg from './images/aboutus.png';

const About = () => {


  const styles = {
    container: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      height: '100vh',
      backgroundImage: `url(${aboutimg})`,
      backgroundSize: 'cover',

    },
    heading: {
      textAlign: 'center',
      color: '#fff',
      fontSize: '32px',
      fontWeight: 'bold',
      textShadow: '2px 2px 4px rgba(0, 0, 0, 0.5)',
      marginTop: '-70vh',

    },



  };
  return (
    <>
      <div style={styles.container}>
        <h1 style={styles.heading}>About Us</h1>
      </div>

      {/* clients */}
      <div id="clientSlider" class="carousel slide" data-bs-ride="carousel">
        <div class="carousel-inner">
          <h1 className="text-white text-center " style={{ margin: '50px' }}> Our Clients</h1>
          <div class="carousel-item active">
            <div class="row justify-content-center">
              <div class="col-md-2">
                <img src={client2} class="img-fluid" alt="Client 1" style={{ fontSize: '20px' }} />
              </div>
              <div class="col-md-2">
                <img src={client4} class="img-fluid" alt="Client 2" />
              </div>
              <div class="col-md-2">
                <img src={client5} class="img-fluid" alt="Client 3" />
              </div>
              <div class="col-md-2">
                <img src={client6} class="img-fluid" alt="Client 4" />
              </div>
              <div class="col-md-2">
                <img src={client7} class="img-fluid" alt="Client 5" />
              </div>

            </div>
          </div>
          <div class="carousel-item">
            <div class="row justify-content-center">
              <div class="col-md-2">
                <img src={client8} class="img-fluid" alt="Client 6" />
              </div>
              <div class="col-md-2">
                <img src={client9} class="img-fluid" alt="Client 7" />
              </div>
              <div class="col-md-2">
                <img src={client10} class="img-fluid" alt="Client 8" />
              </div>
              <div class="col-md-2">
                <img src={client11} class="img-fluid" alt="Client 9" />
              </div>
              <div class="col-md-2">
                <img src={client12} class="img-fluid" alt="Client 10" />
              </div>
            </div>
          </div>
        </div>
        <button class="carousel-control-prev" type="button" data-bs-target="#clientSlider" data-bs-slide="prev">
          <span class="carousel-control-prev-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Previous</span>
        </button>
        <button class="carousel-control-next" type="button" data-bs-target="#clientSlider" data-bs-slide="next">
          <span class="carousel-control-next-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Next</span>
        </button>
      </div>



      {/* service */}

      <section className="services">
        <div className="container">
          <div className="row">
            <div className=" col-md-12 text-center mb-5">
              <h2 className="section-heading" style={{ margin: '50px' }}>Services We Provide</h2>
              <hr className="section-underline" />
            </div>
            <div className="col-md-4">
              <div className="service-item">
                <i className="bi bi-laptop"></i>
                <h3>Design and Printing</h3>
                <p>

                  Great design and print company have two specific goals – to communicate and promote. Excellence requires complete mastery of design print craft and expertise in typography, layout, and photography. 

                  
                </p>
              </div>
            </div>
            <div className="col-md-4">
              <div className="service-item">
                <i className="bi bi-layers"></i>
                <h3>Digital Marketing</h3>
                <p>

                  We aim to show a resistless public presentation to the multitude that adds to the brand value of our loved clients. Our goal is to get straight to the point and cutting out everything that’s not required .

                 
                </p>
              </div>
            </div>
            <div className="col-md-4">
              <div className="service-item">
                <i className="bi bi-chat-dots"></i>
                <h3>Brand Managment</h3>
                <p>

                  We expertise and primary focus to ensure strategic clarity, build brand image, shape culture and attain sustainable market growth and competitive advantage.

                  Corporate identity-This deals with branding through collaterals. 
                  
                </p>
              </div>
            </div>
            <div className="col-md-4">
              <div className="service-item">
                <i className="bi bi-chat-dots"></i>
                <h3>Advertising</h3>
                <p>

                  Advertisement, the root of all sales, product improvements and stabilizing the market value of a particular brand. Without advertising, brands would have a fewer audience.

                  
                </p>
              </div>
            </div>
            <div className="col-md-4">
              <div className="service-item">
                <i className="bi bi-chat-dots"></i>
                <h3>Exhibition and Events</h3>
                <p>

                  To make the grand events more pompous, a 360 coverage is provided to our clients opting for  B2B, B2C meet, corporate branding task, Flags Exhibitions and Events Division offering complete turnkey solutions.

                </p>
              </div>
            </div>
            <div className="col-md-4">
              <div className="service-item">
                <i className="bi bi-chat-dots"></i>
                <h3>Public Relations</h3>
                <p>

                  In a porous world, where news and events race across time and space, media relations ultimately helping in building relationships with the audience is the golden key.  

               
                </p>
              </div>
            </div>
            <div className="col-md-4">
              <div className="service-item">
                <i className="bi bi-chat-dots"></i>
                <h3>Web Designing</h3>
                <p>

                  From the basic to advanced database sites, we promise to meet virtually every need of online communication.

                  Web development- to deal with designing, coding and modifying websites, from layout to function and according to specifications set.
                  .
                </p>
              </div>
            </div>
            <div className="col-md-4">
              <div className="service-item">
                <i className="bi bi-chat-dots"></i>
                <h3>Marketing</h3>
                <p>

                  The key to foster individuality lies in marketing. With the solemn believe that the best thing to do with the best things in life is passing them to others, Atraski serves a broad range of services to provide innovation at every step.

                 
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>

    </>
  )
}

export default About
