import React from 'react'
import Work from './images/image.jpg'
import project1 from './imagegallery/aao.png'
import project2 from './imagegallery/barbeque.png'
import project3 from './imagegallery/ducati.png'
import project4 from './imagegallery/hoichoi.png'
import project5 from './imagegallery/jewel.png'
import project6 from './imagegallery/pepnpure.png'
import mogasun from './imagegallery/mog asun.jpg'
import jewel from './imagegallery/jewel.jpg'


const Work1 = () => {
  const headerStyle = {
    backgroundImage: `url(${Work})`,
    backgroundSize: '1297px',
    backgroundAttachment: 'fixed',
    height: '30vh',
    position: 'relative',
  };

  const headingStyle = {
    color: '#ffffff',
    fontSize: '2rem',
    textTransform: 'uppercase',
    textAlign: 'center',
  };
  return (
    <>
      <header style={headerStyle}>
        <h1 style={headingStyle}>Our Work!</h1>
      </header>






      <section className="work-section py-5">
        <div className="containerw">



          <div className="cardwork">
            <div className="cardwork-image">
              <img src={project1} />
            </div>
            <div className="cardwork-text">
              <p className="cardwork-meal-type">Pep n Pure</p>
              <h2 className="cardwork-title">Budget: 50,000</h2>
              <p className="cardwork-body">Brief:Implementing Digital Marketing for Pep n Pure Results: Company sales increased by 60% .</p>
            </div>

          </div>
          <div className="cardwork">
            <div className="cardwork-image">
              <img src={project2} />
            </div>
            <div className="cardwork-text">
              <p className="cardwork-meal-type">Barbeque Nation</p>
              <h2 className="cardwork-title">Budget: 50,000</h2>
              <p className="cardwork-body">Brief: Launch of “Barbeque in a Box” in North India. Results: Company sales increased by 60%.</p>
            </div>
          </div>
          <div className="cardwork">
            <div className="cardwork-image">
              <img src={project3} />
            </div>
            <div className="cardwork-text">
              <p className="cardwork-meal-type">Hoichoi</p>
              <h2 className="cardwork-title">Budget: 5,00,000</h2>
              <p className="cardwork-body">Brief: Implementing Influencer Marketing for Hoichoi.Results: 20 lakh+ streaming done</p>
            </div>
          </div>
          <div className="cardwork">
            <div className="cardwork-image">
              <img src={project4} />
            </div>
            <div className="cardwork-text">
              <p className="cardwork-meal-type">Ducati Brief</p>
              <h2 className="cardwork-title">Budget: 10,000,00 </h2>
              <p className="cardwork-body">Brief: Women bikers rally and tourism.</p>
            </div>
          </div>
          <div className="cardwork">
            <div className="cardwork-image">
              <img src={project5} />
            </div>
            <div className="cardwork-text">
              <p className="cardwork-meal-type">JewelBox</p>
              <h2 className="cardwork-title">Budget: 10,000</h2>
              <p className="cardwork-body">Brief: Implementing Influencer Marketing for JewelBox. Budget: 10,000</p>
            </div>
          </div>
          <div className="cardwork">
            <div className="cardwork-image">
              <img src={project6} />
            </div>
            <div className="cardwork-text">
              <p className="cardwork-meal-type">Aao Nxt</p>
              <h2 className="cardwork-title">Budget: 20,000</h2>
              <p className="cardwork-body">Results: Increased 5K+ subcribers.</p>
            </div>
          </div>



        </div>
      </section>


      <div class="container">
        <h1 style={{ color: 'white', textAlign: 'center', margin: '70px' }}>Sucessful Projects</h1>
        <div class="row justify-content-center">
          <div class="col-md-6 text-white text-center">
            <h2>Mog Asun Project:</h2>

            <p>Atraski, collaborated with Mog Asun, a Goa based whiskey brand to promote them on Instagram through influencer marketing. We partnered with Goa-based bloggers who have a significant following on the platform. Atraski carefully selected bloggers who fit Mog Asun's target audience and brand image to ensure a successful campaign. The bloggers created engaging content featuring Mog Asun's products, highlighting their uniqueness and availability. Through this partnership, Mog Asun was able to reach a wider audience, increase brand awareness, and boost sales. Atraski's expertise in influencer marketing, combined with the popularity of the bloggers, made this campaign a success for both Mog Asun and Atraski.</p>
          </div>
          <div class="col-md-6">
            <img src={mogasun} class="img-fluid" alt="Our Vision" style={{ width: '60%', height: 'auto', border: '10px', margin: '20px' }} />
          </div>
        </div>
      </div>
      <div class="container">
        <div class="row justify-content-center">

          <div class="col-md-6">
            <img src={jewel} class="img-fluid" alt="Our Vision" style={{ width: '60%', height: 'auto', border: '10px', margin: '20px' }} />
          </div>
          <div class="col-md-6 text-white text-center">
            <h2>JewelBox Project:</h2>

            <p>Atraksi Partnered with JewelBox to promote their Valentine's Day special collections on Instagram through influencer marketing. We collaborated with Kolkata-based lifestyle bloggers who have a substantial following on the platform.To guarantee a successful campaign, Atraski identified bloggers carefully who complemented JewelBox's brand values and target audience. The bloggers produced interesting reels highlighting JewelBox's special Valentine's Day collections, showing their distinctive diamond jewelry designs and features. Through this collaboration, JewelBox was able to raise brand recognition, and boost sales. The success of this campaign was due to Atraski's know-how in influencer marketing and the appeal of the bloggers.</p>
          </div>

        </div>
      </div>






    </>
  )
}

export default Work1
